@columns: 10;
@atom: (100% / @columns);


@offset: 0px;
@offset_tiny: @offset;
@offset_xxs: @offset;
@offset_xs: @offset;
@offset_xsm: @offset;
@offset_sm: @offset;
@offset_md: @offset;
@offset_lg: @offset;

@break_tiny: 310px;
@break_xxs: 359px;
@break_xs: 410px;
@break_xsm: 560px;
@break_sm: 660px;
@break_md: 730px;
@break_lg: 800px;

@tiny: 310px;
@xxs: 359px;
@xs: 410px;
@xsm: 560px;
@sm: 660px;
@md: 730px;
@lg: 800px;


@l: landscape;
@p: portrait;



.wrapper(){
  position: relative;
  max-width: 900px;
  min-width:320px;
  width: 100%;
  max-height: 850px;
  min-height: 320px;
  height: 100%;
  background: @back-body;
  margin: 0 auto;
}



.size(@n){
  @val: 100% / @columns * @n;
  width: ~"calc(@{val} - @{offset})";
}

.size-tiny(@n){
  @val: 100% / @columns * @n;
  .tiny(width, ~"calc(@{val} - @{offset_tiny})");
}

.size-xxs(@n){
  @val: 100% / @columns * @n;
  .xxs(width, ~"calc(@{val} - @{offset_xxs})");
}

.size-xs(@n){
  @val: 100% / @columns * @n;
  .xs(width, ~"calc(@{val} - @{offset_xs})");
}

.size-xsm(@n){
  @val: 100% / @columns * @n;
  .xsm(width, ~"calc(@{val} - @{offset_xsm})");
}

.size-sm(@n){
  @val: 100% / @columns * @n;
  .sm(width, ~"calc(@{val} - @{offset_sm})");
}

.size-md(@n){
  @val: 100% / @columns * @n;
  .md(width, ~"calc(@{val} - @{offset_md})");
}

.size-lg(@n){
  @val: 100% / @columns * @n;
  .lg(width, ~"calc(@{val} - @{offset_lg})");
}




.size-h(@n){
  @val: 100% / @columns * @n;
  height: ~"calc(@{val} - @{offset})";
}

.size-tiny-h(@n){
  @val: 100% / @columns * @n;
  .tiny(height, ~"calc(@{val} - @{offset})");
}

.size-xxs-h(@n){
  @val: 100% / @columns * @n;
  .xxs(height, ~"calc(@{val} - @{offset})");
}

.size-xs-h(@n){
  @val: 100% / @columns * @n;
  .xs(height, ~"calc(@{val} - @{offset})");
}

.size-xsm-h(@n){
  @val: 100% / @columns * @n;
  .xsm(height, ~"calc(@{val} - @{offset})");
}

.size-sm-h(@n){
  @val: 100% / @columns * @n;
  .sm(height, ~"calc(@{val} - @{offset})");
}

.size-md-h(@n){
  @val: 100% / @columns * @n;
  .md(height, ~"calc(@{val} - @{offset})");
}

.size-lg-h(@n){
  @val: 100% / @columns * @n;
  .lg(height, ~"calc(@{val} - @{offset})");
}

/* MIXINS */

.tiny(@name, @value){
  @media screen and (min-width: @break_tiny){
    @{name}: @value;
  }
}

.tiny-o(@orient, @name, @value){
  @media screen and (min-width: @break_tiny) and (orientation: @orient){
    @{name}: @value;
  }
}

.tiny-b(@content){
  @media screen and (min-width: @break_tiny){
    @content();
  }
}

.tiny-block(@orient, @content){
  @media screen and (min-width: @break_tiny) and (orientation: @orient){
    @content();
  }
}

.tiny-h(@height, @orient, @content){
  @media screen and (min-width: @break_tiny) and (min-height: @height) and (orientation: @orient){
    @content();
  }
}

